@import url(https://fonts.googleapis.com/css?family=Bree+Serif&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box;overflow-y:overlay}.App{background-color:#e9e9e9;min-height:100vh;font-size:1rem;color:#333;padding:40px}.logoImg{height:5rem;pointer-events:none;display:inline-block}@media (prefers-reduced-motion: no-preference){.logoImg{height:80px;width:80px}}.logoText{position:relative;padding:1rem 1.2rem;font-size:2rem;font-family:"Bree Serif", serif;letter-spacing:0.1rem;color:#333;line-height:0;white-space:nowrap}.App-header{width:100%;max-width:900px;margin:0 auto;display:flex}.App-body{width:100%;max-width:900px;margin:20px auto}.App-footer{width:100%;max-width:900px;margin:0 auto;display:block;font-size:0.8em;margin-top:20px;color:#666}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.spaceContainer{min-height:500px;border:dotted #fbff7f 1px;background-color:#1e1e27;position:relative;overflow:hidden}.spaceContainer .uiBox{position:absolute;padding:10px;top:10px;left:10px;width:200px;height:400px;border:1px dotted #fff;color:#FFF}

